nav {
  position: fixed;
  top: 0;
  width: 100%;
  text-align: left;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(#ff8177),
    color-stop(0%, #ff867a),
    color-stop(21%, #ff8c7f),
    color-stop(52%, #f99185),
    color-stop(78%, #cf556c),
    to(#b12a5b)
  );
  background-image: -webkit-linear-gradient(
    left,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
  background-image: -o-linear-gradient(
    left,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
  background-image: linear-gradient(
    to right,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
  padding: 5px 0;
  z-index: 2;
  height: 70px;
  -webkit-box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
          box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}
nav > ul {
  display: -ms-flexbox;
  display: flex;
}
nav > ul > li {
  padding: 0 20px;
  max-width: 60px;
}
nav ul li {
  list-style-type: none;
}

nav > ul > li {
  display: inline-block;
  position: relative;
  margin: 0;
}

nav ul ul {
  -webkit-transform: scale(1, 0);
      -ms-transform: scale(1, 0);
          transform: scale(1, 0);
  -webkit-transform-origin: top center;
      -ms-transform-origin: top center;
          transform-origin: top center;
  -webkit-transition: 0.2s ease-out 50ms;
  -o-transition: 0.2s ease-out 50ms;
  transition: 0.2s ease-out 50ms;
  position: absolute;
  top: 100%;
  border: 1px solid rgba(0, 0, 0, 0);
  background: #b12a5b;
  padding: 0;
  width: 100%;
  border-radius: 0 0 3px 3px;
  margin: 0;
}
nav li ul {
  max-width: 88%;
}
nav ul li ul li {
  border-bottom: 2px solid #cf556c;
  width: 100%;
  margin: 5px 0;
}
nav li:hover ul {
  -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
          transform: scale(1, 1);
}

h3 {
  margin: 0;
  background: rgba(177, 42, 91, 0.1);
  width: 90px;
  text-align: center;
  padding: 5px 0;
  border-radius: 3px 3px 0 0;
}



